export default {
	data() {
		return {
			clubId: '',
			feeData: {}
		}
	},
	onLoad(options) {
		this.clubId = options.clubId
		this.$http.get({
			url: '/v1/rest/club/detailsMmembershipFees',
			data: {
				clubId: this.clubId,
				userId: uni.getStorageSync('userInfo').userId
			}
		}).then(resp => {
			// console.log(resp)
			if(resp.status == 200) {
				this.feeData = resp.data
			}
		})
	},
	methods: {
		
	}
}
